@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}


.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 56px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
}

.header_left {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}
.hamburger {
  cursor: pointer;
  margin-left: 10px;
}
.header_logo {
  margin-left: 10px;
  height: 75px;
}

.header_center {
  display: flex;
  justify-content: center;
  width: 40%;
  height: 30px;
}
.header_center > input {
  flex: 1 1;
  border: 1px solid #ccc;
  padding: 2px 6px;
  font-size: 14px;
  outline: none;
}
.searchIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #d3d3d3;
  border-left: none;
  background-color: #f8f8f8;
  color: gray;
  width: 60px;
}
.searchIcon:hover {
  background-color: #e9e7e7;
}

.header_right {
  display: flex;
  align-items: center;
}
.header_right_icon {
  color: rgb(96, 96, 96);
  margin-right: 20px;
  cursor: pointer;
}
.notifications {
  position: fixed;
  top: 56px;
  right: 20px;
  background-color: white;
  opacity: 0.95;
  color: black;
  display: flex;
  flex-direction: column;
  height: 40vh;
  width: 400px;
  overflow-y: scroll;
  border: 1px solid black;
}
.notification {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 4px;
  padding: 4px;
}
.notificationText {
  margin: 10px;
}

.header_right_icon.avatar {
  width: 30px !important;
  height: 30px !important;
}

@media (max-width: 600px) {
  .header_right {
    display: none;
  }
  .hamburger {
    display: none;
  }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header_left {
    margin: 0;
    width: 30%;
  }
  .header_center {
    width: 50%;
    margin-right: 30px;
  }
  .searchIcon {
    width: 30px;
  }
}

.videoSection {
  margin-left: 16vw;
  background-color: #f9f9f9;
  padding: 30px 20px;
  z-index: -1;
}
.videoSection.expand {
  margin-left: 6vw;
}

.videos {
  display: flex;
  flex-wrap: wrap;
}

.videoCard {
  margin-bottom: 40px;
  margin-left: 8px;
  margin-right: 8px;
  width: 23%;
  cursor: pointer;
}

.thumbnail {
  width: 100%;
}

.VideoCardInfo {
  display: flex;
  margin-top: 10px;
  padding-right: 24px;
}
.videoCardText {
  margin-left: 15px;
}
.videoCardText > h4 {
  font-size: 14px;
  margin-bottom: 5px;
}
.videoCardText > p {
  font-size: 14px;
  color: #606060;
}

.videoCardAvatar {
  height: 36px !important;
}

@media (min-width: 900px) and (max-width: 1000px) {
  .videoSection {
    margin-left: 7vw;
  }
}

@media (min-width: 600px) and (max-width: 900px) {
  .videoSection {
    margin-left: 0;
  }

  .videoCard {
    width: 30%;
  }
  .videoCardText > h4 {
    font-size: 12px;
    margin-bottom: 4px;
  }
  .videoCardText > p {
    font-size: 12px;
    color: #606060;
  }
}

@media (max-width: 600px) {
  .videoSection {
    margin-left: 0;
    padding: 0;
  }

  .videos {
    flex-direction: column;
    align-items: center;
  }

  .videoCard {
    width: 90%;
  }
}

.sidebar {
  width: 6vw;
  height: 100vh;
  position: fixed;
  left: 0;
  background-color: white;
}
.sidebar.active {
  width: 16vw;
  position: fixed;
  left: 0;
  overflow-y: scroll;
  height: 100vh;
  z-index: 100;
}

.second {
  display: none;
}
.second.active {
  display: block;
}

.sidebarRow.active {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 12px 20px;
}
.sidebarRow {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

hr {
  margin: 10px 0;
}

.sidebarRowIcon.active {
  color: #606060;
  font-size: large !important;
}
.sidebarRowIcon {
  color: #606060;
}

.sidebarRowTitle.active {
  flex: 1 1;
  margin-left: 20px;
  font-weight: 500;
  font-size: 15px;
}
.sidebarRowTitle {
  margin: 4px 2px;
  font-weight: 500;
  font-size: 10px;
}

.sidebarRow:hover {
  cursor: pointer;
  background-color: #f7f5f5;
}
.sidebarRow.selected {
  background-color: rgba(0, 0, 0, 0.1);
}
.sidebarRow.selected > .sidebarRowTitle {
  font-weight: bold;
}
.sidebarRow.selected > .sidebarRowIcon {
  color: red;
}

.text {
  margin: 20px;
  color: #606060;
}

@media (min-width: 900px) and (max-width: 1000px) {
  .sidebar.active {
    width: 7vw;
    height: 100vh;
    position: fixed;
    left: 0;
    background-color: white;
  }
  .sidebar {
    width: 25vw;
    position: fixed;
    left: 0;
    overflow-y: scroll;
    height: 100vh;
    z-index: 100;
  }

  .sidebarRow.active {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
  }
  .sidebarRow {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 12px 20px;
  }

  .second.active {
    display: none;
  }
  .second {
    display: block;
  }

  .sidebarRowTitle {
    flex: 1 1;
    margin-left: 20px;
    font-weight: 500;
    font-size: 14px;
  }
  .sidebarRowTitle.active {
    margin: 4px 2px;
    font-weight: 500;
    font-size: 10px;
  }

  .sidebarRowIcon {
    color: #606060;
    font-size: large !important;
  }
  .sidebarRowIcon.active {
    color: #606060;
  }
}

@media (min-width: 600px) and (max-width: 900px) {
  .sidebar.active {
    display: none;
  }

  .sidebar {
    width: 25vw;
    position: fixed;
    left: 0;
    overflow-y: scroll;
    height: 100vh;
    z-index: 100;
  }

  .sidebarRow {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 12px 20px;
  }

  .second {
    display: block;
  }

  .sidebarRowTitle {
    flex: 1 1;
    margin-left: 20px;
    font-weight: 500;
    font-size: 14px;
  }

  .sidebarRowIcon {
    color: #606060;
    font-size: large !important;
  }
  .sidebarRowIcon.active {
    color: #606060;
  }
}

@media (max-width: 600px) {
  .second.active {
    display: none;
  }

  .sidebar.active {
    z-index: 100;
    height: 7vh;
    display: flex;
    justify-content: space-evenly;
    background-color: white;
    position: fixed;
    bottom: 0;
    width: 100vw;
  }

  .sidebarRowTitle.active {
    display: none;
  }
  .sidebarRow.selected {
    background-color: white;
  }
  .second {
    display: none;
  }

  .sidebar {
    z-index: 100;
    height: 7vh;
    display: flex;
    justify-content: space-evenly;
    background-color: white;
    position: fixed;
    bottom: 0;
    width: 100vw;
  }

  .sidebarRowTitle {
    display: none;
  }
  .sidebarRow.selected {
    background-color: white;
  }
}

